import React from 'react';

const Icon = (props) => {
  const { number, href, alt, src, size, padding } = props;
  return (
    <span className={`icon ${number}`} style={{ padding: `${padding}` }}>
      <a href={`${href}`}>
        <img src={`${src}`} alt={`${alt}`} height={`${size}`} width={`${size}`} />
      </a>
    </span>
  );
};

export default Icon;
